import debug from "./isDebug.js";
import { __, sprintf } from "@wordpress/i18n";

const isVowel = (letter) => {
  const vowels = "aeiou";
  return vowels.includes(letter.toLowerCase());
};

const getMorthyMessage = (noun) => {
  // Приводим слово к нижнему регистру для упрощения сравнения
  const lowerNoun = noun.toLowerCase();

  let adjective;
  // Наивное определение рода по конкретным словам
  if (lowerNoun === "имя") {
    // "имя" – средний род
    adjective = "действительное";
  } else if (lowerNoun === "фамилия") {
    // "имя" – средний род
    adjective = "действительную";
    noun = "фамилию";
  } else {
    // Для остальных слов можно задать значение по умолчанию
    adjective = "действительный";
  }

  return { [lowerNoun]: [adjective, noun] };
};
// Ждём, пока появится поле (если оно динамически рендерится) и навешиваем собитие, чтобы контролировать ошибку

const waitForField = (selector, callback) => {
  selector = selector.replaceAll("saphali/", "saphali\\/");
  const observer = new MutationObserver(() => {
      const element = document.querySelector(selector);
      if (element) {
          observer.disconnect();
          // console.log(`Элемент ${selector} найден, инициализируем React...`);
          callback(element);
      }
  });

  observer.observe(document.body, { childList: true, subtree: true });
};
// Функция debounce – возвращает обёртку, которая запускает func через delay миллисекунд после последнего вызова
function debounce(func, delay) {
  let timer;
  return function (...args) {
    clearTimeout(timer);
    timer = setTimeout(() => func.apply(this, args), delay);
  };
}
// Функция для сравнения версий
const isVersionLessThan = (versionA, versionB) => {
  const aParts = versionA.split('.').map(Number);
  const bParts = versionB.split('.').map(Number);

  for (let i = 0; i < Math.max(aParts.length, bParts.length); i++) {
      const a = aParts[i] || 0;
      const b = bParts[i] || 0;
      if (a < b) return true;
      if (a > b) return false;
  }
  return false;
};
