const reorderFields = (area, fieldsObj, ascending = true) => {
  const areaContainer = document.querySelector(
    `#${area}.wc-block-components-address-form`
  );
  if (!areaContainer) {
    logDebug(`Container for area #${area} not found.`, 'warn');
    return;
  }
  
  const fieldsArray = Object.entries(fieldsObj).map(([key, val]) => ({
    key,
    index: val.index ?? 0,
    type: val.type ?? "text",
    label: val.label || key,
  }));

  fieldsArray.sort((a, b) =>
    ascending ? a.index - b.index : b.index - a.index
  );

  fieldsArray.forEach((field) => {
    field.key = field.key.replaceAll('saphali/', "saphali\\/");
    let selector = `#${area}-${field.key}`;
    
    let inputEl = document.querySelector(selector);
    if (!inputEl) {
      // Если не найден по ID, попробуем найти по классу (например, для некоторых полей)
      inputEl = document.querySelector(
        `.wc-block-components-address-form__${field.key}`
      );
    }
    if (!inputEl) return;

    // Обновляем label и aria-label, если элемент ещё не обновлён
    if (
      inputEl.hasAttribute("aria-label") &&
      inputEl.getAttribute("aria-label") !== field.label
    ) {
      const parentDiv = inputEl.closest("div");
      const labelElement = parentDiv ? parentDiv.querySelector("label") : null;
      if (labelElement) labelElement.innerHTML = field.label;
      inputEl.setAttribute("aria-label", field.label);
      if (!inputEl.classList.contains("saphli-field-control")) {
        // Выбираем существующий инпут по его ID (например, "billing-first_name")
        ((inputElCb) => {
          inputElCb.classList.add("saphli-field-control");
          if (
            inputElCb &&
            inputElCb.hasAttribute("required") &&
            ["input", "select"].includes(inputElCb.nodeName.toLowerCase())
          ) {
            // Добавляем обработчик события blur
            const _event =
              inputElCb.nodeName.toLowerCase() === "input" ? "blur" : "change";
            inputElCb.addEventListener(_event, (event) => {
              const value = event.target.value.trim();
              if (!value) {
                const label = inputElCb
                  .closest("div")
                  .querySelector("label").textContent;
                const an = isVowel(label[0]) ? "an" : "a";
                // Если поле пустое – устанавливаем ошибку через wc/store/validation
                if (label) {
                  setTimeout(() => {
                    // clearInterval(timer);
                    const MorthyMessage = getMorthyMessage(label.toLowerCase());
                    wp.data
                      .dispatch("wc/store/validation")
                      .setValidationErrors({
                        [inputElCb.getAttribute("id").replace("-", "_")]: {
                          message: sprintf(
                            __(
                              "Please enter %s %s",
                              "saphali-woocommerce-lite"
                            ),
                            an,
                            label.toLowerCase()
                          )
                            .replace(
                              "действительный",
                              MorthyMessage[label.toLowerCase()][0]
                            )
                            .replace(
                              label.toLowerCase(),
                              MorthyMessage[label.toLowerCase()][1]
                            ),
                          hidden: false,
                        },
                      });
                  }, 100);
                }

                  logDebug(
                    "Ошибка установлена: " +
                      inputElCb.getAttribute("id").replace("-", "_") +
                      " пустое",
                    label
                  );
              } else {
                // Если значение заполнено – очищаем ошибку
                wp.data
                  .dispatch("wc/store/validation")
                  .clearValidationErrors([
                    inputElCb.getAttribute("id").replace("-", "_"),
                  ]);
                  logDebug(
                    "Ошибка для " +
                      inputElCb.getAttribute("id").replace("-", "_") +
                      " очищена"
                  );
              }
            });
          }
        })(inputEl);
      }
    }

    // Определяем родительский контейнер для перестановки:
    // Для большинства полей ищем по классу, для поля страны (если type === 'country') – отдельно.

    // if (!inputEl) { inputEl = document.querySelector(`.wc-block-components-address-form__${field.key}`); } if (!inputEl) return;
    let parentWrap;
    if (field.type === "country") {
      parentWrap = inputEl.closest(
        `.wc-block-components-address-form__${field.key}`
      );
    } else {
      parentWrap = inputEl.closest(
        `.wc-block-components-address-form__${field.key}`
      );
    }
    logDebug(`Field: ${field.key}, parentWrap:`, parentWrap);
    if (parentWrap && areaContainer.contains(parentWrap)) {
      areaContainer.appendChild(parentWrap);
      logDebug(
        `Reordered field ${field.key} (type=${field.type}, index=${field.index}) in area ${area}`
      );
  }
  });
};
