const selectedShippingMethod = (select) => {
  // Получаем данные корзины через селектор
  const cartData = select('wc/store/cart')?.getCartData() ?? {};

  // Если нет пакетов доставки — возвращаем пустую строку
  const shippingPackages = cartData.shippingRates;
  if (!shippingPackages?.length) {
    return '';
  }

  // Берем первый пакет доставки
  const { shipping_rates: shippingRates } = shippingPackages[0] || {};
  if (!shippingRates?.length) {
    return '';
  }

  // Ищем выбранный тариф
  const selectedRate = shippingRates.find((rate) => rate.selected);
  return selectedRate?.method_id || 'free_shipping';
};

